import React from "react";
import { useSearchParams } from "react-router-dom";
import { FilterMenu } from "../Components/FilterMenu";
import { PageLayout } from "../Components/PageLayout";
import { AnArchivePreview } from "../Components/Previews/AnArchive.preview";
import { usePublicationsQuery } from "../ReactQuery/publications.queries";
import { usePublicationTypesQuery } from "../ReactQuery/publicationTypes.queries";
import { FRONT_END_ROUTES } from "../Routes";

export function PublicationsPage(): React.ReactElement {
  const [searchParams] = useSearchParams();
  const type = searchParams.get("type");

  const { data: publicationTypes } = usePublicationTypesQuery();

  const { data: anArchives } = usePublicationsQuery(
    type
      ? {
          filters: {
            an_archive_type: {
              $eq: type,
            },
          },
        }
      : {}
  );

  return (
    <PageLayout title="Publications">
      <div className="grid grid-flow-row gap-8">
        <div>
          {!!publicationTypes && (
            <FilterMenu
              menuItems={publicationTypes.map((type) => ({
                label: type.name || "",
                to: `${FRONT_END_ROUTES.publications}?type=${type.id}`,
              }))}
              activeTo={`${FRONT_END_ROUTES.publications}?type=${type}`}
            />
          )}
        </div>
        <div className="grid grid-flow-row gap-4">
          {anArchives?.map((anArchive) => (
            <div>
              <AnArchivePreview anArchive={anArchive} />
            </div>
          ))}
        </div>
      </div>
    </PageLayout>
  );
}
