import { Link } from "react-router-dom";

type FilterMenuProps = {
  menuItems: {
    label: string;
    to: string;
  }[];
  activeTo?: string;
};

export function FilterMenu({ menuItems, activeTo }: FilterMenuProps) {
  return (
    <ul className="flex -mx-4 -my-1 flex-wrap">
      {menuItems.map((item) => (
        <li
          key={item.to}
          className={`mx-4 my-1 underline-offset-4 ${
            activeTo === item.to ? "underline" : "no-underline"
          }`}
        >
          <Link to={item.to}>{item.label}</Link>
        </li>
      ))}
    </ul>
  );
}
