import { PropsWithChildren } from "react";
import { Link } from "react-router-dom";
import { useAnArchiveTypesQuery } from "../ReactQuery/anArchiveTypes.queries";
import { usePublicationTypesQuery } from "../ReactQuery/publicationTypes.queries";
import { FRONT_END_ROUTES } from "../Routes";

type SideMenuItemProps = {
  label: string;
  link: string;
};

export function SideMenuItemLevelOne({
  label,
  link,
  children,
}: PropsWithChildren<SideMenuItemProps>) {
  return (
    <li>
      <Link to={link} className={`block text-lg ${children && "pb-2"}`}>
        {label}
      </Link>
      {children}
    </li>
  );
}

export function SideMenu() {
  const { data: publicationTypes } = usePublicationTypesQuery();
  const { data: anarchiveTypes } = useAnArchiveTypesQuery();

  return (
    <ul className="grid grid-auto-column gap-6 max-w-[300px] text-right">
      <SideMenuItemLevelOne link={FRONT_END_ROUTES.home} label="Accueil" />
      <SideMenuItemLevelOne
        link={FRONT_END_ROUTES.anArchives}
        label="An-archives"
      >
        <ul>
          {anarchiveTypes?.map((type) => (
            <li key={type.id}>
              <Link to={`${FRONT_END_ROUTES.anArchives}?type=${type.id}`}>
                {type.name}
              </Link>
            </li>
          ))}
        </ul>
      </SideMenuItemLevelOne>

      <SideMenuItemLevelOne
        link={FRONT_END_ROUTES.publications}
        label="Publications"
      >
        <ul>
          {publicationTypes?.map((type) => (
            <li key={type.id}>
              <Link to={`${FRONT_END_ROUTES.publications}?type=${type.id}`}>
                {type.name}
              </Link>
            </li>
          ))}
        </ul>
      </SideMenuItemLevelOne>

      <SideMenuItemLevelOne
        link={FRONT_END_ROUTES.academique}
        label="Acadmique"
      />

      <SideMenuItemLevelOne link={FRONT_END_ROUTES.presse} label="Presse" />

      <SideMenuItemLevelOne link={FRONT_END_ROUTES.contact} label="Contact" />
    </ul>
  );
}
