import { Routes, Route } from "react-router-dom";
import { AcademiquePage } from "./Pages/AcademiquePage";
import { AnArchivesPage } from "./Pages/AnArchives.page";
import { HomePage } from "./Pages/Home.page";
import { PressePage } from "./Pages/Presse.page";
import { PublicationsPage } from "./Pages/Publications.page";
import { FRONT_END_ROUTES } from "./Routes";

export default function Router() {
  return (
    <Routes>
      <Route path={FRONT_END_ROUTES.anArchives} element={<AnArchivesPage />} />
      <Route
        path={FRONT_END_ROUTES.publications}
        element={<PublicationsPage />}
      />
      <Route path={FRONT_END_ROUTES.academique} element={<AcademiquePage />} />
      <Route path={FRONT_END_ROUTES.presse} element={<PressePage />} />
      <Route path={FRONT_END_ROUTES.home} element={<HomePage />} />
    </Routes>
  );
}
